/* jshint ignore:start */

if (!runningTests) {
  require("jackalope/app")["default"].create({"name":"jackalope","version":"1.0.0+b4a31ff9"});
}

define('~fastboot/app-factory', ['jackalope/app', 'jackalope/config/environment'], function(App, config) {
  App = App['default'];
  config = config['default'];

  return {
    'default': function() {
      return App.create(config.APP);
    }
  };
});


/* jshint ignore:end */
